<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <div>
            <el-input placeholder="" v-model="queryInfo.query" @change="getOrderList">
              <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
            </el-input>
          </div>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderlist" border stripe>
        <!--  -->
        <el-table-column label="#" type="index"></el-table-column>
        <!--  -->
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_fapiao_content" width="120px"></el-table-column>
        <el-table-column label="" prop="order_fapiao_title" width="120px">
          <template v-slot="{row}">
            <el-tag v-if="row.order_fapiao_title===''" class="iconfont icon-webicon08 "></el-tag>
            <el-tag type="success" v-else class="iconfont icon-gongsi4"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="order_price" width="100px"></el-table-column>
        <el-table-column label="" prop="pay_status" width="120px">
          <template v-slot="{row}">
            <el-tag type="danger" v-if="row.pay_status==='0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="80px"></el-table-column>
        <el-table-column label="" width="180px">
          <template v-slot="{row}">
            {{row.create_time|dateFormat('yyyy-MM-dd hh:mm:ss')}}
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" width="180px">
          <template>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top">
              <el-button type="primary" icon="iconfont icon-changeAddress" size="mini" @click="showAddressDialog"></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top">
              <el-button type="success" icon="iconfont icon-wuliuxinxi" size="mini" @click="showProgressBox"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 30]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editAddressDialogVisible" width="50%" @close="editAddressDialogClosed">
      <!--  -->
      <el-form :model="editAddressForm" :rules="editAddressFormRules" ref="editAddressFormRef" label-width="100px">
        <el-form-item label="/" prop="location">
          <el-cascader v-model="editAddressForm.location" :options="cityData" :props="locationProps" @change="handleChange"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="detailedAddress">
          <el-input v-model="editAddressForm.detailedAddress"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAddressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editAddress"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="progressDialogVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressData" :key="index" :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from '../../tools/citydata.js'
export default {
    data () {
        return {
            //  
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 5
            },
            // 
            orderlist: [],
            // 
            total: 0,
            //  /  
            editAddressDialogVisible: false,
            //  /  
            progressDialogVisible: false,
            // 
            editAddressForm: {
                detailedAddress: '',
                location: []
            },
            // 
            editAddressFormRules: {
                // 
                detailedAddress: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                // /
                location: [
                    {
                        required: true,
                        message: '/'
                    }
                ]
            },
            // 
            cityData,
            // 
            locationProps: {
                expandTrigger: 'hover',
                label: 'label',
                value: 'value',
                children: 'children'
            },
            // 
            progressData: []

        }
    },
    created () {
    // 
        this.getOrderList()
    },
    methods: {
    // 
        async getOrderList () {
            //  
            const res = await this.$http.get('orders', { params: this.queryInfo })
            //  200
            if (res.meta.status !== 200) {
                // 
                return this.$message.error('!')
            }
            // data
            this.orderlist = res.data.goods
            // 
            this.total = res.data.total
        },
        //  
        handleSizeChange (newSize) {
            this.queryInfo.pagesize = newSize
            this.getOrderList()
        },
        //  
        handleCurrentChange (newPage) {
            this.queryInfo.pagenum = newPage
            this.getOrderList()
        },
        //  
        showAddressDialog () {
            this.editAddressDialogVisible = true
        },
        //  
        handleChange () {

        },
        //  
        editAddressDialogClosed () {
            // 
            this.editAddressForm.location = []
            this.$refs.editAddressFormRef.resetFields()
        },
        //  
        editAddress () {
            // 
            this.$refs.editAddressFormRef.validate(valid => {
                if (!valid) {
                    //  
                    return this.$message.error('!')
                }
                // 
                this.$message.success('!')
                // 
                this.getOrderList()
                // 
                this.editAddressDialogVisible = false
            })
        },
        //  
        async showProgressBox () {
            const res = await this.$http.get('/kuaidi/1106975712662')
            if (res.meta.status !== 200) {
                return this.$message.error('!')
            }
            this.progressData = res.data
            this.$message.success('!')
            this.progressDialogVisible = true
        }
    }
}
</script>

<style lang="less" scoped>
.iconfont {
  font-size: 12px;
}
</style>